y: fallouty;\n  font-weight: 400;\n  accent-color: hsl(var(--primary));\n  background: var(--bg);\n  overscroll-behavior: none;\n  overflow: clip;\n  height: 100dvh;\n  width: 100dvw;\n}\n\n.brush {\n  display: flex;\n  align-items: center;\n  font-family: \"brush\";\n\n  .big {\n    font-size: 4.5rem;\n  }\n  .medium {\n    font-size: 3.5rem;\n  }\n}\n.sans {\n  font-family: sans-serif;\n  font-weight: 600;\n  font-size: 3.5rem;\n  font-stretch: condensed;\n}\n\nheader {\n  display: flex;\n\n  background: none;\n  border-right: solid 2px;\n  padding: 2rem 0;\n\n  #logo {\n    font-size: 2.5rem;\n    position: relative;\n    padding-inline: 2rem;\n    padding-bottom: 2rem;\n    height: 120px;\n\n    .brush {\n      rotate: -15deg;\n      translate: 0 -1rem;\n    }\n    .sans {\n      position: absolute;\n      left: 45%;\n      top: 25%;\n    }\n  }\n}\n\nnav {\n  min-height: 0;\n  flex-grow: 1;\n  display: flex;\n\n  & > ul {\n    display: grid;\n    grid-template-rows: 1fr min-content;\n    max-height: 100%;\n    width: 100%;\n    gap: 1rem;\n\n    .nav-top {\n      min-height: 0;\n      display: grid;\n      grid-template-rows: min-content min-content minmax(0, 1fr);\n    }\n  }\n\n  & li:not(.nav-section):not(#new-task):not(#new-project-btn) {\n    position: relative;\n    padding-block: 0.2rem;\n\n    &:hover:before,\n    &:focus:before,\n    &.active:before {\n      content: \"\\25B2\";\n      position: absolute;\n      rotate: 90deg;\n      top: 17%;\n      left: 0.7rem;\n    }\n  }\n  & #projects {\n    display: grid;\n    grid-template-rows: min-content minmax(0, min-content) 1fr;\n\n    ul {\n      overflow-x: auto;\n      margin-bottom: 0.5rem;\n\n      div {\n        display: flex;\n\n        &:hover .delete-project-dialog {\n          visibility: visible;\n        }\n      }\n\n      .delete-project-dialog {\n        visibility: hidden;\n        font-family: Arial;\n        font-size: 0.8rem;\n        line-height: 0;\n        width: 2rem;\n        padding-inline: 0;\n      }\n\n      & .project-btn {\n        padding-right: 0.5rem;\n        &::before {\n          content: \"#\";\n          font-style: italic;\n        }\n      }\n    }\n  }\n\n  & #settings button::before {\n    content: \"\";\n  }\n\n  li.active {\n    background: hsl(var(--primary));\n    box-shadow: var(--glow);\n    border-radius: 0;\n\n    &:before {\n      content: \"\";\n    }\n\n    button {\n      color: var(--bg);\n    }\n  }\n  button {\n    border: none;\n    display: flex;\n    gap: 0.7rem;\n    align-items: center;\n  }\n\n  & #new-task-btn::before,\n  & #today-btn::before,\n  & #credits-btn::before {\n    border: solid 1.5px;\n    height: 1rem;\n    width: 1rem;\n    line-height: 1.1rem;\n    text-align: center;\n    padding-left: 1px;\n  }\n  & #new-task-btn::before {\n    content: \"+\";\n    border-radius: 100%;\n    font-size: 0.9rem;\n  }\n  & #credits-btn::before {\n    content: \"i\";\n    border-radius: 100%;\n    font-size: 0.8rem;\n    font-family: sans;\n    font-weight: 900;\n    line-height: 0.8rem;\n  }\n  & #today-btn::before {\n    content: \"13\" !important;\n    border-top-width: 4px;\n    font-size: 0.6rem;\n    font-style: normal !important;\n    display: flex;\n    align-items: anchor-center;\n    justify-content: center;\n  }\n  & #new-project {\n    display: flex;\n    justify-content: center;\n    position: relative;\n    padding: 0;\n    inset: 0;\n\n    & #new-project-btn {\n      display: flex;\n      border-radius: 100%;\n      font-size: 1.1rem;\n      border: solid 1.5px;\n      width: 1.5rem;\n      padding: 0;\n      height: 1.5rem;\n      justify-content: center;\n      padding-top: 4px;\n      padding-left: 0.5px;\n    }\n  }\n}\n\n.h-container {\n  display: flex;\n  justify-content: center;\n  position: relative;\n  padding-block: 0.5rem;\n\n  &:before {\n    content: \"\";\n    position: absolute;\n    top: 50%;\n    left: 0.5rem;\n    width: calc(100% - 1rem);\n    height: 1px;\n    background: h